<template>
<div>
    <button class="btn" @click="start()">开始录音1.14</button>
    <button class="btn" @click="stop()">结束录音2</button>
    <audio id="a"  preload="metadata"  :src="src"  controls = "controls"></audio>
</div>
</template>
<script>
export default {
    name: 'luyin',
    data() {
        return{
            src: ''
        }
    },
    methods: {
        start() {
            console.log(1);
            window.dsBridge.call('testAsyn', 'startRecord', res => {
                console.log('开始录音了')
                console.log(res);
            })
        },
        stop() {
            let _this = this;
            console.log(12);
            
            console.log(_this.src);
            window.dsBridge.call('testAsyn', 'stopRecord', res => {
                console.log('结束录音了')
                res = JSON.parse(res);
                let file = _this.dataURLtoFile(res.data.url, 'liuyin.mp3', 'blob');
                // _this.down(file, 'liuyin.mp3')
                _this.src = URL.createObjectURL(file)
            })
        },

        //将base64转换为文件或者文件流
        dataURLtoFile(dataurl, filename, type) {
            var arr = dataurl.split(',');
            var mime = 'mp3';
            var bstr = atob(arr[0]);
            var n = bstr.length;
            var u8arr = new Uint8Array(n);
            while(n--){
                u8arr[n] = bstr.charCodeAt(n);
            }
            if(type == 'file') {
                return new File([u8arr], filename, {type:mime});
            } else if (type == 'blob') {
                return new Blob([u8arr], {type:mime});
            }
        },
        // 下载
        down(blob, fileName){
            const objectUrl = URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.setAttribute('style', 'display:none');
            a.setAttribute('href', objectUrl);
            a.setAttribute('download', fileName);
            a.click();
            URL.revokeObjectURL(objectUrl);
        }
    }
}
</script>
